// Notepad
// Task bar

#game-page {
    #Game {
        position: relative;
        width: 100%;
        height: 100%;

        #bg {
            min-height: 100%;
            width: 100%;
            height: 100%;
            z-index: -4;
        }

        #Objects div,
        #Decors div {
            img {
                position: absolute;
                bottom: 0;
                height: auto;
                background-size: cover;
                -webkit-transition: .3s ease-in-out;
                -o-transition: .3s ease-in-out;
                transition: .3s ease-in-out;
                // pointer-events: none; // Empecher la selection d'un objet
            }
        }

        #Objects div {
            -webkit-transition: .3s ease-in-out;
            -webkit-filter: drop-shadow(.01rem .01rem .01rem &black);
            filter: drop-shadow(.01rem .01rem .01rem &black);
        }
    }
}
