body {
    width: 100vw;
    height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    cursor: none;
}

.cursor {
    position: fixed;
    left: 0;
    top: 0;
    pointer-events: none;
}

.cursor--small {
    width: 20px;
    height: 20px;
    left: -10px;
    top: -10px;
    border-radius: 50%;
    z-index: 99;
    background: $white;
}

.cursor--canvas {
    width: 100vw;
    height: 100vh;
    z-index: 100;
}

canvas[resize] {
    width: 100%;
    height: 100%;
}